.info-section {
  padding-bottom: 490px;

  h2 {
    margin-top: 100px;
    margin-bottom: 50px;
    text-align: center;
    color: #000;
  }
  .list-info {
    display: flex;
    flex-direction: column;
    li {
      padding: 10px 0;
      font-weight: 700;
      font-size: 20px;
      color: #000;
      border-top: 1px solid #000;
    }
  }
}
