.product-slider {
  margin-bottom: 100px;
  display: flex;
  background-color: #fff;
  .list-slide {
    width: 200px;
  }
  li {
    position: relative;
    display: flex;
    flex-direction: column;
    white-space: nowrap;
    flex-shrink: 0;
    padding: 10px 20px;
    // animation: go-left-right 8s infinite linear alternate;

    .title {
      font-size: 12px;
      font-weight: 600;
      text-align: left;
    }
    .subtitle {
      margin-bottom: 5px;
      font-size: 10px;
    }
    .oldprice {
      font-size: 10px;
      text-align: end;
      text-decoration: line-through;
    }
    .newprice {
      margin-bottom: 10px;
      font-weight: bold;
      font-size: 12px;
      text-align: end;
    }
    button {
      width: 100%;
      padding: 5px 10px;
      color: #fff;
      text-align: center;
      font-weight: bold;
      font-size: 12px;
      background-color: #ffb700;
      border-radius: 10px;
      box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.5);
    }
    button:hover {
      background-color: #d89c02;
    }
    button:focus {
      background-color: #d89c02;
    }
  }

  .img-box {
    padding: 10px;
    margin-bottom: 5px;
    width: 100px;
    height: 100px;
    border: 1px solid #cacaca;
    border-radius: 10px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}
