.categories-section {
  padding-bottom: 100px;
  .search-list {
    display: block;
    flex-direction: column;
    li {
      max-width: 100%;
      height: auto;
      a {
        font-weight: normal;
      }
    }
    li:hover {
      transition: background-color 0.3s ease-in-out;
      background-color: #edecec;
    }
    li:focus {
      transition: background-color 0.3s ease-in-out;
      background-color: #edecec;
    }
  }
  ul {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    li {
      margin-bottom: 20px;
      position: relative;
      max-width: 165px;
      height: 180px;
      img {
        width: 100%;
      }
      a {
        font-size: 18px;
        font-weight: bold;
      }
      span {
        position: absolute;
        top: 8px;
        left: 15px;
      }
    }
  }
}
